import React, { useState, useEffect } from 'react';
import connect from '@vkontakte/vk-connect';
import View from '@vkontakte/vkui/dist/components/View/View';
import ScreenSpinner from '@vkontakte/vkui/dist/components/ScreenSpinner/ScreenSpinner';
import '@vkontakte/vkui/dist/vkui.css';
import axios from 'axios';
import fetchJsonp from 'fetch-jsonp';


import Home from './panels/Home';
import Persik from './panels/Persik';


function  hash(){
	try
    {
        let encoded = decodeURIComponent(window.location.hash.split("#")[1]);
        return encoded;
    }catch(err){
		console.log(err);
	}
}



const App = () => {
	const [activePanel, setActivePanel] = useState('home');
	const [fetchedUser, setUser] = useState(null);
	const [getToken, setToken] = useState(null);
	const [popout, setPopout] = useState(<ScreenSpinner size='large' />);

	useEffect(() => {
		connect.subscribe(({ detail: { type, data }}) => {
			if (type === 'VKWebAppUpdateConfig') {
				const schemeAttribute = document.createAttribute('scheme');
				schemeAttribute.value = data.scheme ? data.scheme : 'client_light';
				document.body.attributes.setNamedItem(schemeAttribute);
			}

            if (type === 'VKWebAppAccessTokenReceived') {
                setToken(data.access_token);
			}
		});



        connect.send("VKWebAppGetAuthToken", {"app_id": 7177213, "scope": "wall,photos"});

		async function fetchData() {
			const user = await connect.sendPromise('VKWebAppGetUserInfo');
			setUser(user);
			setPopout(null);
		}
		fetchData();
	}, []);

	async function go(){



        let upload_server = await connect.sendPromise("VKWebAppCallAPIMethod",{"method": "photos.getWallUploadServer", "request_id": "32test", "params": {"v":"5.102", "access_token":getToken}});
        let {upload_url, user_id} = upload_server.response;


   ///     axios.get('//publes.ru/mini_apps/vk_upload.php', {params:{upload_url, user_id, photo:hash()}}).then(post);


        fetchJsonp('//publes.ru/mini_apps/vk_upload.php?upload_url='+upload_url+"&user_id="+user_id+"&photo="+hash())
            .then(post)
			.catch(function(ex) {console.log('parsing failed', ex)})







        async function post(upload) {
            console.log({upload});

            const save = await connect.sendPromise("VKWebAppCallAPIMethod",{"method": "photos.saveWallPhoto", "request_id": "33test", "params": {"v":"5.102", "access_token":getToken, "user_id":user_id, photo:upload.data.photo, hash:upload.data.hash, server:upload.data.server, caption:"@art_privet(Создать такой арт)"}});

            let {id,owner_id} = save.response[0];
            let attachments = "photo"+owner_id+"_"+id

            //photos.saveWallPhoto
            console.log({save})


            const post = await connect.sendPromise("VKWebAppShowWallPostBox",{"message":"# @art_privet(Арт)",attachments});
            console.log({post});
        }

	};



	return (
		<View activePanel={activePanel} popout={popout}>
			<Home id='home' fetchedUser={fetchedUser} go={go} hash={hash()} />
			<Persik id='persik' go={go} />
		</View>
	);
}

export default App;

